<template>
    <div class="manager">
        <div class="field-part">
            <info-list :dataCopy="formData" :map="map" :filter="filter"></info-list>
        </div>
        <div class="field-part">
            <mt-field label="备注名称" v-model="formData.mark"></mt-field>
        </div>
        <div class="field-warn">
            备注名称例如：妈妈家的电费
        </div>
        <!-- 按钮 -->
        <div class="field-action">
            <mt-button type="default" size="large" @click="doSave">保存</mt-button>
        </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui-rem-dpr';
    import * as services from '@/services/life-cell';
    import InfoList from '../info-list';
    export default {
        name: 'manager',
        components: {
            InfoList
        },
        data() {
            return {
                formData: {
                    name: ''
                },
                map: {
                    companyName: '缴费公司',
                    no: '用户编号',
                    name: '用户姓名'
                },
                filter: ['item', 'href', 'area', 'id', 'company', 'mark']
            };
        },
        created() {
            this.formData = Object.assign({}, this.$route.params.info);
        },
        methods: {
            doSave() {
                this.$router.go(-1);
            }
        }
    };

</script>

<style lang="scss" scoped>

</style>


