import ajax from './ajax';
import api from './api';
/*  获取生活缴费入口cell列表 */
export function getLifeCell(data) {
    return ajax.post(api.getLifeCell, data);
}
/*  获取地区 */
export function getPowerArea(data) {
    return ajax.post(api.getPowerArea, data);
}
/* 获取电力公司 */
export function getPowerCompany(data) {
    let id = data.id;
    return new Promise(function (resolve, reject) {
        ajax.post(api.getPowerCompany, data).then((res) => {
            let list = res.data.filter((item) => {
                if (item.id === id) {
                    return item.company;
                }
            });
            resolve(list);
        }, (res) => {
            reject(res);
        }).catch((err) => {
            reject(err);
        });
    });
}

/* 获取缴费学校  */
export function getSchoolList(data) {
    let id = data.id;
    return new Promise(function (resolve, reject) {
        ajax.post(api.getSchoolList, data).then((res) => {
            let list = res.data.filter((item) => {
                if (item.id === id) {
                    return item.school;
                }
            });
            resolve(list);
        }, (res) => {
            reject(res);
        }).catch((err) => {
            reject(err);
        });
    });
}

/*  获取电费信息 */
export function getPowerinfo(data) {
    return ajax.post(api.getPowerinfo, data);
}

/*  获取交通罚款信息 */
export function getTrafficInfo(data) {
    return ajax.post(api.getTrafficInfo, data);
}

/*  获取缴费历史信息 */
export function getHistoryList(data) {
    return ajax.post(api.getHistoryList, data);
}

/*  获取学费缴费信息 */
export function getSchoolInfo(data) {
    return ajax.post(api.getSchoolInfo, data);
}


