<template>
    <div class="school">
        <div class="field-part">

            <mt-cell class="mint-select" title="缴费地区">
                <select class="mint-field-core" v-model="formData.area">
                    <option v-for="item in areaList" key="item.id" :value="item.id">
                        {{item.area}}
                    </option>
                </select>
            </mt-cell>

            <mt-cell class="mint-select" title="学校名称">
                <select class="mint-field-core" v-model="formData.school">
                    <option v-for="item in schoolList" key="item.id" :value="item.id">
                        {{item.name}}
                    </option>
                </select>
            </mt-cell>

            <mt-field label="学号" type="number" placeholder="请输入学号" v-model="formData.no"></mt-field>
        </div>

        <div class="field-action">
            <mt-button type="default" size="large" @click="doQuery">查询</mt-button>
        </div>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui-rem-dpr';
    import * as services from '@/services/life-cell';
    export default {
        name: 'school',
        data() {
            return {
                formData: {
                    area: '',
                    school: '',
                    no: ''
                },
                areaList: null,
                schoolList: null
            };
        },
        created() {
            let ls = this.$ls.getItem('school');
            if (ls !== null && ls !== undefined) {
                this.formData = Object.assign({}, this.$ls.getItem('school'));
            }
            services.getPowerArea()
                .then(res => {
                    this.areaList = res.data;
                });
        },
        watch: {
            'formData.area': {
                handler: 'getSchoolList',
                deep: true
            }
        },
        methods: {
            getSchoolList() {
                if (this.formData.area !== '') {
                    let params = { id: this.formData.area };
                    services.getSchoolList(params)
                        .then(res => {
                            if (res.length > 0) {
                                this.schoolList = res[0].school;
                            }
                        });
                }
            },
            doQuery() {
                let _data = Object.assign({}, this.formData);
                console.log(_data);
                if (this.$validate.isEmpty(_data.area)) {
                    this.doMsg('请选择缴费地区');
                    return false;
                }
                if (this.$validate.isEmpty(_data.school)) {
                    this.doMsg('请选择学校名称');
                    return false;
                }
                if (this.$validate.isEmpty(_data.no)) {
                    this.doMsg('请输入学号');
                    return false;
                }
                this.$router.push({ name: 'LifeSchoolInfo' });
            },
            doMsg(msg) {
                Toast({
                    message: msg,
                    iconClass: 'error',
                    duration: 1000
                });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector') {
                this.$ls.setItem('school', this.formData);
            } else {
                this.$ls.removeItem('school');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>

</style>


