<template>
    <div class="heat-pay">
        <module-pay ref="child" :warn="warn" module="heat"></module-pay>

        <div class="field-action">
            <mt-button type="default" size="large" @click="doPay">立即支付</mt-button>
        </div>

        <dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
    </div>
</template>

<script>
    import ModulePay from '../module/pay';
    import { Toast } from 'mint-ui-rem-dpr';
    export default {
        name: 'heat-pay',
        components: {
            ModulePay
        },
        mounted() {
            // 控制不显示哪些字段
            this.$refs.child.filter = ['area', 'company', 'companyName', 'no', 'name', 'href', 'id', 'mark', 'href'];
        },
        data() {
            return {
                warn: '备注名称例如：妈妈家的水费',
                dialogSmsVerify: {
                    title: '输入短信验证码',
                    btnConfirmText: '确定转账',
                    btnGetText: '获取验证码'
                }
            };
        },
        methods: {
            doPay() {
                let _data = Object.assign({}, this.$refs.child.formData);
                if (this.$validate.isEmpty(_data.account)) {
                    this.doMsg('请选择支付账号');
                    return false;
                }
                if (this.$validate.isEmpty(_data.pwd)) {
                    this.doMsg('请输入交易密码');
                    return false;
                }
                let { item, area, name, company, no } = this.$ls.getItem('heat');
                let obj = {
                    item: item,
                    name: name,
                    area: area,
                    company: company,
                    no: no,
                    href: 'LifeHeat'
                };
                this.$refs.dialogSmsVerify.show().then((data) => {
                    alert('短信验证码验证成功...');
                    this.$store.dispatch('showHistory', obj);
                    this.$store.dispatch('setHistory', { item: 'Heat', status: true });
                    this.$router.push({ name: 'LifeResult', query: { p: -4 } });
                }, (data) => {
                });
            },
            doMsg(msg) {
                Toast({
                    message: msg,
                    iconClass: 'error',
                    duration: 1000
                });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] !== from.meta['groupName'] && to.meta['groupName'] !== 'selector') {
                this.$ls.removeItem('heat');
            }
            next(true);
        }
    };

</script>


