<template>
    <div class="heat">
        <module-index ref="child" module="heat" moduleCompany="热力公司"></module-index>

        <div class="field-action">
            <mt-button type="default" size="large" @click="doQuery">查询</mt-button>
        </div>
    </div>
</template>

<script>
    import ModuleIndex from '../module/index';
    import { Toast } from 'mint-ui-rem-dpr';
    export default {
        name: 'heat',
        components: {
            ModuleIndex
        },
        methods: {
            doQuery() {
                let _data = Object.assign({}, this.$refs.child.life);
                if (this.$validate.isEmpty(_data.area)) {
                    this.doMsg('请选择缴费地区');
                    return false;
                }
                if (this.$validate.isEmpty(_data.company)) {
                    this.doMsg('请选择供电公司');
                    return false;
                }
                if (this.$validate.isEmpty(_data.no)) {
                    this.doMsg('请输入用户编号');
                    return false;
                }
                this.$router.push({ name: 'LifeHeatInfo' });
            },
            doMsg(msg) {
                Toast({
                    message: msg,
                    iconClass: 'error',
                    duration: 1000
                });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector') {
                this.$ls.setItem('heat', this.$refs.child.life);
            } else {
                this.$ls.removeItem('heat');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>

</style>


