<template>
    <div class="traffic-pay">
        <div class="field-part">
            <mt-field label="缴费金额" readonly disableClear v-model="formData.money"></mt-field>
        </div>

        <div class="field-part select-account">
            <cell title="支付账户" @click.native="showAccount()" v-model="formData.account" isLink></cell>
            <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount "></selector-payment-account>
        </div>

        <div class="field-part">
            <mt-field label="交易密码" v-model="formData.pwd"></mt-field>
        </div>

        <div class="field-action">
            <mt-button type="default" size="large" @click="doPay">立即支付</mt-button>
        </div>

        <dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui-rem-dpr';
    export default {
        name: 'traffic-pay',
        data() {
            return {
                formData: {
                    money: '',
                    id: '',
                    account: '',
                    pwd: '',
                    mark: ''
                },
                dialogSmsVerify: {
                    title: '输入短信验证码',
                    btnConfirmText: '确定转账',
                    btnGetText: '获取验证码'
                },
                selectorPaymentAccount: {
                    title: '选择付款账号',
                    checked: {},
                    showAddBtn: true,
                    accountList: [
                        {
                            id: 1,
                            name: '郭振宇',
                            account: '625228***0089'
                        },
                        {
                            id: 2,
                            name: '郭振宇2',
                            account: '625228***0025'
                        },
                        {
                            id: 3,
                            name: '郭振宇3',
                            alias: '',
                            account: '625228***0109',
                            bankName: '安徽农金'
                        }
                    ]
                }
            };
        },
        created() {
            let ls = this.$ls.getItem('traffic');
            this.formData.money = ls.total_amount;
        },
        methods: {
            showAccount() {
                this.$refs.selectorPaymentAccount.show().then((data) => {
                    this.selectorPaymentAccount.checked = data.item;
                    this.formData.id = this.selectorPaymentAccount.checked.id;
                    this.formData.account = this.selectorPaymentAccount.checked.account;
                }, (data) => {
                });
            },
            doPay() {
                let _data = Object.assign({}, this.formData);
                if (this.$validate.isEmpty(_data.account)) {
                    this.doMsg('请选择支付账户');
                    return false;
                }
                if (this.$validate.isEmpty(_data.pwd)) {
                    this.doMsg('请输入交易密码');
                    return false;
                }
                this.$refs.dialogSmsVerify.show().then((data) => {
                    alert('短信验证码验证成功...');
                    this.$router.push({ name: 'LifeResult', query: { p: -4 } });
                }, (data) => {
                });
            },
            doMsg(msg) {
                Toast({
                    message: msg,
                    iconClass: 'error',
                    duration: 1000
                });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] !== from.meta['groupName'] && to.meta['groupName'] !== 'selector') {
                this.$ls.removeItem('traffic');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .select-account {
        background-color: #fff;
    }
</style>


