<template>
    <div class="power-info">
        <div class="field-part" v-if="infoList !== null">
            <info-list :dataCopy="infoList" :map="map"></info-list>
        </div>

        <div class="field-part">
            <mt-field label="缴费金额" readonly disableClear v-model="money"></mt-field>
        </div>

        <div class="field-action">
            <mt-button type="default" size="large" @click="pay">立即交费</mt-button>
        </div>
    </div>
</template>

<script>
    import * as servers from '../../../services/life-cell';
    import InfoList from '../info-list';
    export default {
        name: 'power',
        components: {
            InfoList
        },
        data() {
            return {
                infoList: {},
                map: {
                    item: '缴费项目',
                    companyName: '供电公司',
                    no: '用户编号',
                    name: '用户姓名',
                    money: '应缴金额'
                },
                money: ''
            };
        },
        created() {
            /** 获取cell数据 **/
            servers.getPowerinfo()
                .then(response => {
                    this.infoList = response.data.power[0];
                    if (this.infoList !== null) {
                        this.money = this.infoList.money;
                    }
                });
        },
        methods: {
            pay() {
                this.$router.push({ name: 'LifePowerPay' });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector') {
                this.$store.dispatch('keepPowerInfo', this.infoList);
            } else {
                this.$store.dispatch('clearPowerInfo');
            }
            next(true);
        }
    };

</script>


