<template>
    <div class="traffic">
        <div class="field-part">
            <mt-field label="处罚决定书编号" v-model="illegal_no"></mt-field>
        </div>

        <div class="field-warn">
            <p>
                温馨提示：
            </p>
            <p>
                1、通过电子渠道缴费暂不支持缴费凭证打印功能，如需缴费凭证请至柜面办理缴费业务。
            </p>
            <p>
                2、该缴费业务只支持安徽省内的交通罚款缴纳。
            </p>
        </div>

        <div class="field-action">
            <mt-button type="default" size="large" @click="doQuery">查询</mt-button>
        </div>
    </div>
</template>

<script>
    import * as services from '@/services/life-cell';
    import { Toast } from 'mint-ui-rem-dpr';
    export default {
        name: 'traffic',
        data() {
            return {
                illegal_no: ''
            };
        },
        created() {
            let ls = this.$ls.getItem('traffic');
            if (ls !== null && ls !== undefined) {
                this.illegal_no = ls.illegal_no;
            }
        },
        methods: {
            doQuery() {
                if (this.$validate.isEmpty(this.illegal_no)) {
                    this.doMsg('请输入处罚决定书编号');
                    return false;
                }
                this.$router.push({ name: 'LifeTrafficInfo' });
            },
            doMsg(msg) {
                Toast({
                    message: msg,
                    iconClass: 'error',
                    duration: 1000
                });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector') {
                this.$ls.setItem('traffic', { illegal_no: this.illegal_no });
            } else {
                this.$ls.removeItem('traffic');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>

</style>


