<template>
    <div class="history-detail">
        <div class="main">
            <div class="hd">
                <div class="title">
                    <img src="../../../assets/img/life/power.png" width="32" height="32">
                    <p v-text="title"></p>
                </div>
                <div class="amount">
                    <p v-text="detailList.amount"></p>
                </div>
            </div>
            <div>
                <info-list :data-copy="detailList" :map="map" :filter="filter"></info-list>
            </div>
        </div>
    </div>
</template>

<script>
    import * as services from '@/services/life-cell';
    import InfoList from '../info-list';
    export default {
        name: 'history-detail',
        components: {
            InfoList
        },
        data() {
            return {
                detailList: '',
                map: {
                    item: '缴费项目',
                    name: '用户名称',
                    remark: '备注',
                    no: '用户编号',
                    account: '支付账户',
                    serial_number: '交易流水',
                    illegal_no: '处罚决定书编号',
                    code: '违章代码',
                    driver_no: '驾驶证编号',
                    late_amount: '滞纳金',
                    total_amount: '缴纳总金额',
                    call: '缴费号码'
                },
                filter: ['id', 'amount', 'item_id', 'carrieroperator', 'company']
            };
        },
        computed: {
            title() {
                if (this.detailList.item_id === 'traffic') {
                    return '交通罚款';
                } else if (this.detailList.item_id === 'phone') {
                    return this.detailList.carrieroperator;
                } else {
                    return this.detailList.company;
                }
            }
        },
        created() {
            console.log(this.$route.params.id);
            services.getHistoryList({ showLoading: '加载中...' })
                .then(res => {
                    let data = res.data.filter(item => {
                        return item.id === this.$route.params.id;
                    });
                    this.detailList = data[0];
                });
        }
    };

</script>

<style lang="scss" scoped>
    .main {
        background-color: #fff;

        .hd {
            margin: 0 20%;
            margin-bottom: 20px;
            padding: 30px;

            .title {
                text-align: center;

                img {
                    vertical-align: middle;
                    display: inline-block;
                }

                p {
                    display: inline-block;
                    vertical-align: middle;
                    font-size: 14px;
                    color: #000;
                }
            }

            .amount {
                text-align: center;

                p {
                    font-size: 16px;
                    color: #000;
                }
            }
        }
    }
</style>


