<template>
    <div class="heat-info">
        <module-info ref="child" module="heat"></module-info>

        <div class="field-action">
            <mt-button type="default" size="large" @click="pay">立即交费</mt-button>
        </div>
    </div>
</template>

<script>
    import * as services from '@/services/life-cell';
    import ModuleInfo from '../module/info';
    export default {
        name: 'heat-info',
        components: {
            ModuleInfo
        },
        mounted() {
            this.$refs.child.map.companyName = "热力公司";
        },
        methods: {
            pay() {
                this.$router.push({ name: 'LifeHeatPay' });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector') {
                let ls = this.$ls.getItem('heat');
                let obj = Object.assign({}, ls, this.$refs.child.infoList);
                this.$ls.setItem('heat', obj);
            } else {
                this.$ls.removeItem('heat');
            }
            next(true);
        }
    };

</script>


