<template>
    <div class="power">
        <div class="field-part">
            <mt-cell class="mint-select" title="缴费地区">
                <select name="" id="" class="mint-field-core" v-model="power.area">
                    <option v-for="item in areaList" key="item.id" :value="item.id">
                        {{item.area}}
                    </option>
                </select>
            </mt-cell>

            <mt-cell class="mint-select" title="供电公司">
                <select name=" " id=" " class="mint-field-core" v-model="power.company">
                    <option v-for="item in companyList" key="item.id" :value="item.id">
                        {{item.name}}
                    </option>
                </select>
            </mt-cell>

            <mt-field label="用户编号" placeholder="请输入用户编号" v-model="power.no"></mt-field>
        </div>


        <div class="field-action">
            <mt-button type="default" size="large" @click="doQuery">查询</mt-button>
        </div>

    </div>
</template>

<script>
    import * as services from '@/services/life-cell';
    import { Toast } from 'mint-ui-rem-dpr';
    export default {
        name: 'power',
        data() {
            return {
                power: {
                    area: '',
                    company: '',
                    no: ''
                },
                areaList: [],
                companyList: []
            };
        },
        watch: {     // 监听地区下拉框变化
            'power.area': {
                handler: 'getCompanyData',
                deep: true
            }
        },
        created() {
            let info = this.$route.params.info;
            if (info !== undefined && info !== null) {
                this.$store.dispatch('keepPowerInfo', info);
            }
            this.power = Object.assign({}, this.$store.state.life.power);
            services.getPowerArea()
                .then(res => {
                    this.areaList = res.data;
                });
        },
        methods: {
            getCompanyData() {
                let params = { id: this.power.area };
                services.getPowerCompany(params)
                    .then(res => {
                        if (res.length > 0) {
                            this.companyList = res[0].company;
                        }
                    });
            },
            doQuery() {
                let _data = Object.assign({}, this.power);
                if (this.$validate.isEmpty(_data.area)) {
                    this.doMsg('请选择缴费地区');
                    return false;
                }
                if (this.$validate.isEmpty(_data.company)) {
                    this.doMsg('请选择供电公司');
                    return false;
                }
                if (this.$validate.isEmpty(_data.no)) {
                    this.doMsg('请输入用户编号');
                    return false;
                }
                this.$router.push({ name: 'LifePowerInfo' });
            },
            doMsg(msg) {
                Toast({
                    message: msg,
                    iconClass: 'error',
                    duration: 1000
                });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector') {
                this.$store.dispatch('keepPowerInfo', this.power);
            } else {
                this.$store.dispatch('clearPowerInfo');
            }
            next(true);
        }
    };

</script>


