<template>
    <div class="phone">
        <!--<div class="field-part">-->
        <div class="field-group">
            <input type="text" class="phone-field" placeholder="请输入手机号码" v-model=" phone.call ">
            <div class="btn-selector btn-seletor-payee">
                <i class="icon" @click="addres"></i>
            </div>
            <div class="yys-wrap">
                <p v-text="yys"></p>
            </div>
        </div>
        <!--</div>-->
        <div class="phone-amount">
            <div class="cell-top">
                选择充值金额
            </div>
            <div class="amount-pannel-group">
                <div v-for="amount in amounts" class="amount-pannel" :class="{'active': active === amount.id}" @click="selectAmount(amount)">
                    <p>{{amount.val}}</p>
                    <p>{{amount.desc}}</p>
                </div>
            </div>
        </div>

        <div class="field-warn">
            <p>
                温馨提示：
            </p>
            <p>
                1、业务缴费受理时间为01:00-23:00,请您合理安排缴费时间，由此带来的不便，敬请谅解！
            </p>
            <p>
                2、通过电子渠道缴费不支持发票凭证打印功能。
            </p>
        </div>

        <div class="field-action">
            <mt-button type="default" size="large" @click="recharge">充值</mt-button>
        </div>
    </div>
</template>

<script>
    import ModuleIndex from '../module/index';
    import { Toast } from 'mint-ui-rem-dpr';
    export default {
        name: 'phone',
        data() {
            return {
                phone: {
                    call: '',
                    amount: ''
                },
                yys: '',    // 显示运营商（可能包括姓名）
                active: '',
                amounts: [
                    {
                        id: '30',
                        val: '30元',
                        desc: '售价30元'
                    }, {
                        id: '50',
                        val: '50元',
                        desc: '售价50元'
                    }, {
                        id: '100',
                        val: '100元',
                        desc: '售价100元'
                    }, {
                        id: '200',
                        val: '200元',
                        desc: '售价200元'
                    }, {
                        id: '300',
                        val: '300元',
                        desc: '售价300元'
                    }, {
                        id: '500',
                        val: '500元',
                        desc: '售价500元'
                    }
                ]
            };
        },
        created() {
            let ls = this.$ls.getItem('phone');
            if (ls !== null && ls !== undefined) {
                this.phone.call = ls.call;
            }
        },
        watch: {
            'phone.call': 'validate'
        },
        methods: {
            selectAmount(item) {
                this.phone.amount = item.id;
                this.active = item.id;
            },
            validate(val) {
                let str = val.substring(0, 11);
                if (str.length === 11) {
                    if (!this.$validate.isPhone(val)) {
                        this.yys = "手机号码格式错误";
                    } else {
                        this.yys = '王小锤 中国移动';
                    }
                }
                if (str.length < 11) {
                    this.yys = "";
                }
                this.phone.call = str;
            },
            recharge() {
                let _data = Object.assign({}, this.phone);
                if (this.$validate.isEmpty(_data.call)) {
                    this.doMsg('请输入手机号码', 'error');
                    return false;
                }
                if (!this.$validate.isPhone(_data.call)) {
                    this.doMsg('手机号码格式错误', 'error');
                    return false;
                }
                if (this.$validate.isEmpty(_data.amount)) {
                    this.doMsg('请选择充值金额', 'error');
                    return false;
                }
                this.$router.push({ name: 'LifePhonePay' });
            },
            doMsg(msg, cl) {
                Toast({
                    message: msg,
                    iconClass: cl,
                    duration: 1000
                });
            },
            addres() {
                this.doMsg('调用系统通讯录', 'mintui mintui-success');
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector') {
                this.$ls.setItem('phone', this.phone);
            } else {
                this.$ls.removeItem('phone');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .field-group {
        position: relative;
        background-color: #fff;

        .phone-field {
            height: 84px;
            width: 100%;
            border: none;
            outline: none;
            text-indent: 12px;
            font-size: 16px;
            color: #8f8f94;

            &:-ms-input-placeholder {
                color: #8f8f94;
            }

            &::-webkit-input-placeholder {
                color: #8f8f94;
            }
        }

        .btn-seletor-payee {
            background-color: inherit;
            .icon {
                background-image: url('../../../assets/img/selector/icon-address-book.png');
            }
        }

        .yys-wrap {
            padding: 0 0 10px 12px;
        }
    }

    .phone-amount {
        position: relative;
        background-color: #fff;
        @include mini-border(bottom,
        #333,
        before) @include mini-border(top,
        #d9d9d9,
        after) .cell-top {
            margin: 0 20px;
            padding: 20px 0 0 0;
            font-size: 12px;
            font-weight: 600;
        }

        .amount-pannel-group {
            margin: 20px 2%;
            padding: 0 0 20px 0;

            .amount-pannel {
                display: inline-block;
                margin: 1.16%;
                padding: 30px;
                width: 31%;
                text-align: center;
                border: 1px solid #ddd;
                border-radius: 2px;
            }
            p {
                white-space: nowrap;
            }
        }

        .active {
            background-color: #ccc;
            color: #fff;
        }
    }
</style>


