<template>
    <div class="module">
        <div class="field-part">
            <info-list :dataCopy="infoList" :map="map"></info-list>
        </div>

        <div class="field-part select-account">
            <cell title="支付账户" @click.native="showAccount()" v-model="formData.account" isLink></cell>
            <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount "></selector-payment-account>
        </div>

        <div class="field-part">
            <mt-field label="交易密码" placeholder="请输入交易密码" v-model="formData.pwd"></mt-field>
        </div>

        <div class="field-action">
            <mt-button type="default" size="large" @click="doPay">立即支付</mt-button>
        </div>

        <dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui-rem-dpr';
    import * as servers from '../../../services/life-cell';
    import InfoList from '../info-list';
    export default {
        name: 'module',
        components: {
            InfoList
        },
        created() {
            servers.getPowerinfo()
                .then(response => {
                    this.infoList = response.data.phone[0];
                });
        },
        data() {
            return {
                infoList: '',
                map: {
                    call: '手机号码',
                    company: '运营商',
                    amount: '支付金额'
                },
                formData: {
                    account: '',
                    pwd: ''
                },
                dialogSmsVerify: {
                    title: '输入短信验证码',
                    btnConfirmText: '确定转账',
                    btnGetText: '获取验证码'
                },
                selectorPaymentAccount: {
                    title: '选择付款账号',
                    checked: {},
                    showAddBtn: true,
                    accountList: [
                        {
                            id: 4,
                            name: '郭振宇',
                            alias: '郭振宇',
                            account: '625228***0089',
                            bankName: '安徽农金'
                        },
                        {
                            id: 5,
                            name: '郭振宇2',
                            alias: '郭振宇2',
                            account: '625228***0025',
                            bankName: '安徽农金'
                        },
                        {
                            id: 6,
                            name: '郭振宇3',
                            alias: '',
                            account: '625228***0109',
                            bankName: '安徽农金'
                        }
                    ]
                }
            };
        },
        methods: {
            showAccount() {
                this.$refs.selectorPaymentAccount.show().then((data) => {
                    this.selectorPaymentAccount.checked = data.item;
                    this.formData.account = this.selectorPaymentAccount.checked.account;
                }, (data) => {
                });
            },
            doPay() {
                let _data = Object.assign({}, this.formData);
                if (this.$validate.isEmpty(_data.account)) {
                    this.doMsg('请选择支付账号');
                    return false;
                }
                if (this.$validate.isEmpty(_data.pwd)) {
                    this.doMsg('请输入交易密码');
                    return false;
                }
                this.$refs.dialogSmsVerify.show().then((data) => {
                    alert('短信验证码验证成功...');
                    this.$router.push({ name: 'LifeResult', query: { p: -3 } });
                }, (data) => {
                });
            },
            doMsg(msg) {
                Toast({
                    message: msg,
                    iconClass: 'error',
                    duration: 1000
                });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] !== from.meta['groupName'] && to.meta['groupName'] !== 'selector') {
                this.$ls.removeItem('phone');
            }
            next(true);
        }
    };

</script>


