<template>
    <div class="power-do-pay">
        <div class="field-part">
            <info-list :dataCopy="infoList" :map="map" :filter="filter"></info-list>
        </div>

        <div class="field-part select-account">
            <cell title="支付账户" @click.native="showAccount()" v-model="formData.account" isLink></cell>
            <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount "></selector-payment-account>
        </div>

        <div class="field-part">
            <mt-field label="交易密码" v-model="formData.pwd"></mt-field>
        </div>

        <div class="field-part">
            <mt-field label="备注名称" v-model="formData.mark"></mt-field>
        </div>

        <div class="field-warn">
            备注名称例如：妈妈家的电费
        </div>

        <div class="field-action">
            <mt-button type="default" size="large" @click="doPay">立即支付</mt-button>
        </div>


    </div>
</template>

<script>
    import InfoList from '../info-list';
    import { Toast } from 'mint-ui-rem-dpr';
    export default {
        name: 'power-do-pay',
        components: {
            InfoList
        },
        data() {
            return {
                infoList: {},
                map: {
                    item: '缴费项目',
                    money: '支付金额'
                },
                filter: ['area', 'company', 'companyName', 'no', 'name', 'id', 'mark', 'href'],
                formData: {
                    account: '',
                    pwd: '',
                    mark: ''
                },
                selectorPaymentAccount: {
                    title: '选择付款账号',
                    checked: {},
                    showAddBtn: true,
                    accountList: [
                        {
                            id: 1,
                            name: '郭振宇',
                            account: '625228***0089'
                        },
                        {
                            id: 2,
                            name: '郭振宇2',
                            account: '625228***0025'
                        },
                        {
                            id: 3,
                            name: '郭振宇3',
                            alias: '',
                            account: '625228***0109',
                            bankName: '安徽农金'
                        },
                        {
                            id: 4,
                            name: '郭振宇',
                            alias: '郭振宇',
                            account: '625228***0089',
                            bankName: '安徽农金'
                        },
                        {
                            id: 5,
                            name: '郭振宇2',
                            alias: '郭振宇2',
                            account: '625228***0025',
                            bankName: '安徽农金'
                        },
                        {
                            id: 6,
                            name: '郭振宇3',
                            alias: '',
                            account: '625228***0109',
                            bankName: '安徽农金'
                        }
                    ]
                }
            };
        },
        created() {
            this.infoList = Object.assign({}, this.$store.state.life.power);
        },
        methods: {
            showAccount() {
                this.$refs.selectorPaymentAccount.show().then((data) => {
                    this.selectorPaymentAccount.checked = data.item;
                    this.formData.account = this.selectorPaymentAccount.checked.account;
                }, (data) => {
                });
            },
            doPay() {
                let _data = Object.assign({}, this.formData);
                if (this.$validate.isEmpty(_data.account)) {
                    this.doMsg('请选择支付账号');
                    return false;
                }
                if (this.$validate.isEmpty(_data.pwd)) {
                    this.doMsg('请输入交易密码');
                    return false;
                }
                let { item, area, name, company, companyName, no } = this.$store.state.life.power;
                let obj = {
                    item: item,
                    company: company,
                    companyName: companyName,
                    name: name,
                    area: area,
                    no: no,
                    href: 'LifePower'
                };
                this.$store.dispatch('showHistory', obj);
                this.$store.dispatch('setHistory', { item: 'Power', status: true });
                this.$router.push({ name: 'LifeResult', query: { p: -4 } });
            },
            doMsg(msg) {
                Toast({
                    message: msg,
                    iconClass: 'error',
                    duration: 1000
                });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] !== from.meta['groupName'] && to.meta['groupName'] !== 'selector') {
                this.$store.dispatch('clearPowerInfo');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .select-account {
        background-color: #fff;
    }
</style>


