<template>
    <div class="school-info">
        <div class="field-part field-list" v-for="item in infoList" v-if="infoList !== null">
            <div @click="pay(item)">
                <mt-cell title="学校名称" :value="item.schoolName"></mt-cell>
                <mt-cell title="学号" :value="item.no"></mt-cell>
                <mt-cell title="姓名" :value="item.name"></mt-cell>
                <mt-cell title="班级" :value="item.grade"></mt-cell>
                <mt-cell title="缴费总额" :value="item.total_money"></mt-cell>
            </div>
            <cell title="缴费明细" @click.native="showDetail(item)" isLink select></cell>
            <!-- 缴费明细 -->
            <transition name="pay-detail">
                <info-list class="info-list" v-if="detail === item.id" :dataCopy="item.detail" :map="map"></info-list>
            </transition>
            <!-- 类别 -->
            <mt-cell class="mint-select" title="类别">
                <select @change="onChange(item)" class="mint-field-core" v-model="select">
                    <option v-for="(item, key) in item.detail" key="item.id" :value="key">
                        {{selcetList[key]}}
                    </option>
                </select>
            </mt-cell>
            <mt-field label="缴费金额" readonly disableClear v-model="item.detail[select]"></mt-field>

        </div>
    </div>
</template>

<script>
    import InfoList from '../info-list';
    import { Toast } from 'mint-ui-rem-dpr';
    import * as services from '@/services/life-cell';
    export default {
        name: 'school-info',
        components: {
            InfoList
        },
        data() {
            return {
                infoList: {},
                map: {
                    schoolName: '学校名称',
                    no: '学号',
                    name: '姓名',
                    grade: '班级',
                    totla_money: '应收合计',
                    book_amount: '书费',
                    learn_amount: '学费',
                    stay_amount: '住宿费',
                    insurance_amount: '保险费用',
                    derate_amount: '减免费用'
                },
                selcetList: {
                    book_amount: '书费',
                    learn_amount: '学费',
                    stay_amount: '住宿费',
                    derate_amount: '减免费用',
                    insurance_amount: '保险费用'
                },
                select: '',
                money: '',
                detail: null    // 显示哪一条缴费明细
            };
        },
        created() {
            /** 获取缴费信息数据 **/
            services.getSchoolInfo()
                .then(response => {
                    this.infoList = response.data;
                });
        },
        methods: {
            showDetail(item) {
                this.detail = this.detail === null ? item.id : null;
            },
            onChange(item) {
                console.log(this.select);
                console.log(item);
                // this.money = item.detail[this.select];
            },
            pay(item) {
                let ls = this.$ls.getItem('school');
                let obj = Object.assign({}, ls, item);
                this.$ls.setItem('school', obj);
                this.$router.push({ name: 'LifeSchoolPay' });
            }
        }
    };

</script>

<style lang="scss" scoped>
    .field-list {
        margin: 20px;

        .info-list {
            background-color: #ccc;
        }

        .pay-detail-enter-active {
            animation: fadein .6s;
        }

        .pay-detail-leave-active {
            animation: fadeout .6s;
        }

        @keyframes fadein {
            0% {
                transform: scaleY(0);
                opacity: 0;
            }

            100% {
                transform: scaleY(1);
                opacity: 1;
            }
        }

        @keyframes fadeout {
            0% {
                transform: scaleY(1);
                opacity: 1;
            }
            100% {
                transform: scaleY(0);
                opacity: 0;
            }
        }
    }
</style>


