<template>
    <div class="power">
        <div class="field-result">
            <div class="result-icon">
                <i class="mintui mintui-success"></i>
            </div>

            <div class="result-name">交易成功</div>
            <div class="result-desc">缴费申请已受理具体到账时间以合作方系统为主</div>
        </div>

        <div class="field-action">
            <mt-button type="default" size="large" @click="finish">完成缴费</mt-button>
        </div>
    </div>
</template>

<script>
    import * as servers from '../../services/life-cell';
    export default {
        name: 'power',
        data() {
            return {
                infoList: null,
                money: ''
            };
        },
        methods: {
            finish() {
                this.$router.go(this.$route.query.p);
            }
        }

    };

</script>

<style lang="scss" scoped>
    .field-result {
        text-align: center;
        padding: 20%;
        div + div {
            margin-top: 10%;
        }
        .result-icon {
            font-size: 16px;
        }
        .result-name {
            font-size: 16px;
            font-weight: bold;
        }
        .result-desc {
            padding: 0 10%;
            font-weight: bold;
            color: #888;
        }
    }
</style>


