<template>
    <div class="power-do-pay">
        <div class="field-part">
            <info-list :dataCopy="infoList" :map="map" :filter="filter"></info-list>
        </div>

        <div class="field-part select-account">
            <cell title="支付账户" @click.native="showAccount()" v-model="formData.account" isLink></cell>
            <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount "></selector-payment-account>
        </div>

        <div class="field-part">
            <mt-field label="交易密码" v-model="formData.pwd"></mt-field>
        </div>

        <div class="field-part">
            <mt-field label="备注名称" v-model="formData.mark"></mt-field>
        </div>

        <div class="field-warn">
            {{warn}}
        </div>
    </div>
</template>

<script>
    import InfoList from '../info-list';
    export default {
        name: 'power-do-pay',
        props: {
            warn: [String],
            module: [String]
        },
        components: {
            InfoList
        },
        data() {
            return {
                infoList: {},
                map: {
                    item: '缴费项目',
                    money: '支付金额'
                },
                filter: [],
                formData: {
                    account: '',
                    pwd: '',
                    mark: ''
                },
                selectorPaymentAccount: {
                    checked: {},
                    showAddBtn: true,
                    showWarn: true,
                    accountList: [
                        {
                            id: 4,
                            name: '郭振宇',
                            alias: '郭振宇',
                            account: '625228***0089',
                            bankName: '安徽农金'
                        },
                        {
                            id: 5,
                            name: '郭振宇2',
                            alias: '郭振宇2',
                            account: '625228***0025',
                            bankName: '安徽农金'
                        },
                        {
                            id: 6,
                            name: '郭振宇3',
                            alias: '',
                            account: '625228***0109',
                            bankName: '安徽农金'
                        }
                    ]
                }
            };
        },
        created() {
            let ls = this.$ls.getItem(this.module);
            this.infoList = Object.assign({}, ls);
        },
        methods: {
            showAccount() {
                this.$refs.selectorPaymentAccount.show().then((data) => {
                    this.selectorPaymentAccount.checked = data.item;
                    this.formData.account = this.selectorPaymentAccount.checked.account;
                }, (data) => {
                });
            }
        }
    };

</script>

<style lang="scss" scoped>
    .select-account {
        background-color: #fff;
    }
</style>


