<template>
    <div class="traffic-info">
        <div class="field-part">
            <info-list :dataCopy="infoList" :map="map"></info-list>
        </div>

        <div class="field-action">
            <mt-button type="default" size="large" @click="next">下一步</mt-button>
        </div>

    </div>
</template>

<script>
    import InfoList from '../info-list.vue';
    import * as services from '@/services/life-cell';
    export default {
        name: 'traffic-info',
        components: {
            InfoList
        },
        data() {
            return {
                infoList: null,
                map: {
                    person: '当事人',
                    driver_card_No: '驾驶证编号',
                    code: '违章代码',
                    date: '处罚日期',
                    illegal_amount: '违法金额',
                    lata_fee: '滞纳金',
                    total_amount: '缴纳总金额'
                }
            };
        },
        created() {
            services.getTrafficInfo()
                .then(res => {
                    this.infoList = res.data[0];
                });
        },
        methods: {
            next() {
                this.$router.push({ name: 'LifeTrafficPay' });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector') {
                // this.$store.dispatch('keepTrafficInfo', this.infoList);
                let ls = this.$ls.getItem('traffic');
                let obj = Object.assign({}, this.infoList, ls);
                this.$ls.setItem('traffic', obj);
            } else {
                // this.$store.dispatch('clearTrafficInfo');
                this.$ls.removeItem('traffic');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>

</style>


