<template>
    <div class="module">
        <div class="field-part">

            <mt-cell class="mint-select" title="缴费地区">
                <select class="mint-field-core" v-model="life.area">
                    <option v-for="item in areaList" selected="item.id === life.area" key="item.id" :value="item.id">
                        {{item.area}}
                    </option>
                </select>
            </mt-cell>

            <mt-cell class="mint-select" :title="moduleCompany">
                <select class="mint-field-core" v-model="life.company">
                    <option v-for="item in companyList" key="item.id" :value="item.id">
                        {{item.name}}
                    </option>
                </select>
            </mt-cell>

            <mt-field label="用户编号" type="number" placeholder="请输入用户编号" v-model="life.no"></mt-field>
        </div>
    </div>
</template>

<script>
    import * as services from '@/services/life-cell';
    import { Toast } from 'mint-ui-rem-dpr';
    export default {
        name: 'module',
        props: ['module', 'moduleCompany'],
        data() {
            return {
                life: {
                    area: '',
                    company: '',
                    no: ''
                },
                areaList: [],
                companyList: []
            };
        },
        created() {
            let info = this.$route.params.info; // 判断是否是历史缴费记录
            let ls = this.$ls.getItem(this.module);
            // console.log(ls);
            if (info !== undefined && info !== null) {
                ls = Object.assign({}, info);
            }
            if (ls !== null && ls !== undefined) {
                this.life = Object.assign({}, ls);
            }
            services.getPowerArea()
                .then(res => {
                    this.areaList = res.data;
                });
        },
        watch: {     // 监听地区下拉框变化
            'life.area': {
                handler: 'getCompanyData',
                deep: true
            }
        },
        methods: {
            getCompanyData() {
                if (this.life.area !== '') {
                    let params = { id: this.life.area };    // 生产会加上当前缴费项目字段
                    services.getPowerCompany(params)
                        .then(res => {
                            if (res.length > 0) {
                                this.companyList = res[0].company;
                            }
                        });
                }
            }
        }
    };

</script>


