<template>
    <div class="history">
        <div class="cell-top">
            <div class="cell-top-left">
                六月
            </div>
        </div>

        <cell @click.native="doDetail(item)" ref="cell" v-for="item in cellList" key="item.id" :title="item.item" :value="item.amount"
            :label="setValue(item)" hdTitle="今天" hdDesc="6-24" isLink>
            <img src="../../../assets/img/life/power.png" slot="icon" width="24" height="24">
        </cell>
    </div>
</template>

<script>
    import { Toast } from 'mint-ui-rem-dpr';
    import * as services from '@/services/life-cell';
    export default {
        name: 'history',
        data() {
            return {
                cellList: null
            };
        },
        created() {
            services.getHistoryList({ showLoading: '加载中...' })
                .then(response => {
                    this.cellList = response.data;
                });
        },
        methods: {
            setValue(item) {
                let mark = item.remark || "";
                let name = item.name;
                if(item.item_id === 'phone') {
                    name = item.carrieroperator;
                }
                return mark === "" ? name : mark + "  |  " + name;
            },
            doDetail(item) {
                this.$router.push({ name: 'LifeHistoryDetail', params: { id: item.id } });
            }
        }
    };

</script>

<style lang="scss" scoped>
    .cell-top {
        position: relative;
        padding: 10px 0 10px 20px;
        font-size: 14px;
        @include mini-border(bottom, #d9d9d9, before);
    }
</style>


