<template>
    <div class="life">
        <!-- 社区e银行入口 -->
        <div class="page-entry">
            <div class="entry-left">
                <p>{{ad.module}}</p>
                <p>{{ad.desc}}</p>
            </div>
            <div class="entry-right">
                <span class="entry-desc">
                    <p>{{ad.goods}}</p>
                    <p>{{ad.discount}}</p>
                </span>
                <span class="entry-img">
                    <img src="../../assets/img/default.png" width="100%" height="150">
                </span>
            </div>
        </div>

        <!-- cell头部信息 -->
        <div class="cell-top">
            <div class="cell-top-left">
                生活缴费
                <div class="cell-top-right">
                    <router-link to="history" tag="span" class="life-history" append>
                        记录
                    </router-link>
                    <span class="life-manager" @click="manager">
                        {{managerText}}
                    </span>
                </div>
            </div>
        </div>

        <!-- 常用功能列表 -->
        <div class="offen">
            <template v-for="item in historyList" v-if="historyList.length > 0">
                <div class="cell-list">
                    <v-touch v-on:swipeleft="onSwipeLeft(item)" v-on:swiperight="swiperight(item)">
                        <cell @click.native="forward(item)" :title="item.item" :label="setValue(item)" isLink>
                            <img src="../../assets/img/life/power.png" slot="icon" width="24" height="24">
                            <span slot="btn">
                                <div ref="swiperBtn" class="swiper-btn" v-if="doManager(item)">
                                    <span v-for="list in swipers" @click.stop.prevent="list.handler(item)" v-text="list.content"></span>
                </div>
                </span>
                </cell>
                </v-touch>
        </div>
        </template>
    </div>

    <!-- 功能列表 -->
    <template v-for="item in cellList" v-if="!history[item.name]">
        <mt-cell key="item.id" :title="item.title" :value="item.value" :to="item.href" is-link>
            <img src="../../assets/img/life/power.png" slot="icon" width="24" height="24">
        </mt-cell>
    </template>

    <!-- 按钮 -->
    <div class="field-action">
        <mt-button type="default" size="large" @click="getMore">更多缴费</mt-button>
    </div>

    </div>
</template>

<script>
    import * as servers from './../../services/life-cell';
    import { mapGetters } from 'vuex';
    import { MessageBox } from 'mint-ui-rem-dpr';
    import delegate from 'delegate-to';
    export default {
        name: 'life',
        data() {
            return {
                cellList: null,
                all: false,
                ad: {
                    module: '社区e银行',
                    desc: '安徽农金品质认证',
                    goods: '四库春雪',
                    discount: '低至5折'
                },
                swipers: [
                    {
                        content: '修改备注',
                        handler: (item) => this.editInfo(item)
                    }, {
                        content: '删除',
                        handler: (item) => this.delInfo(item)
                    }
                ],
                swiper: []
            };
        },
        computed: {
            ...mapGetters(['historyList', 'history']),
            managerText() {
                if (this.all) {
                    return '取消管理';
                } else {
                    return '管理';
                }
            }
        },
        created() {
            /** 获取cell数据 **/
            servers.getLifeCell()
                .then(response => {
                    this.cellList = response.data;
                });
        },
        mounted() {
            let self = this;
            document.querySelector('.life').addEventListener('click', function (e) {
                let show = document.querySelector('.swiper-btn');
                if (e.target.parentNode.className !== 'swiper-btn' && e.target.className !== 'life-manager' && show !== null) {
                    self.swiper = [];
                }
            });
        },
        watch: {
            'swiper': 'setAll'
        },
        methods: {
            // 判断cell列表title下面的显示值
            setValue(item) {
                let mark = item.mark || "";
                let name = item.name;
                return mark === "" ? name : mark + "  |  " + name;
            },
            // 更多缴费
            getMore() {
                this.$router.push({ name: "LifeMore" });
            },
            // 监听swiper状态
            setAll(val) {
                if (val.length === 0) {
                    this.all = false;
                }
            },
            // 通过缴费记录跳转
            forward(item) {
                this.$router.push({ name: item.href, params: { info: item } });
            },
            // 管理/取消管理
            manager() {
                this.all = !this.all;
                this.swiper = [];
                if (this.all) {
                    for (let value of this.historyList) {
                        this.swiper.push(value.id);
                    }
                }
            },
            doManager(item) {
                if (this.swiper.indexOf(item.id) !== -1) {
                    return true;
                } else {
                    return false;
                }
            },
            // 编辑该条记录
            editInfo(item) {
                console.log("666");
                this.$router.push({ name: 'LifeManager', params: { info: item } });
            },
            // 删除该条记录
            delInfo(item, event) {
                MessageBox.confirm('<span>删除账户后再次缴费需要重新填写账户信息<span><br/><span>是否确认删除该缴费账户?</span>').then(action => {
                    // console.log(action);
                    let index = this.swiper.indexOf(item.id);
                    this.swiper.splice(index, 1);
                }, () => {
                });
            },
            // 向左滑动
            onSwipeLeft(item) {
                // 第一种：可以同时滑动多个
                /** let index = this.swiper.indexOf(item.id);
                if (index !== -1) {
                    this.swiper[index] = item.id;
                } else {
                    this.swiper.push(item.id);
                } **/
                // 第二种：单条滑动下最多显示一条滑动
                this.swiper = [];
                this.swiper.push(item.id);
            },
            // 向右滑动
            swiperight(item) {
                let index = this.swiper.indexOf(item.id);
                this.swiper.splice(index, 1);
            }
        },
        beforeRouteLeave(to, from, next) {
            if (this.swiper.length !== 0 && to.name !== 'LifeManager') {
                next(false);
            } else {
                next(true);
            }
        }
    };

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
    .page-entry {
        padding: 30px;
        display: flex;
        background: #fff;
        .entry-left {
            padding-right: 30px;
            p+p {
                margin-top: 20px;
            }
        }
        .entry-right {
            flex: 1;
            display: flex;
            padding-left: 30px;
            border-left: 1px solid #999;
            .entry-desc {
                flex: 1;
                padding-right: 30px;
                p+p {
                    margin-top: 20px;
                }
            }
            .entry-img {
                flex: 1;
                img {
                    height: 100px;
                    width: 100%;
                }
            }
        }
    }

    .cell-top {
        margin: 10px 20px;
        padding: 20px 0 0 0;
        font-size: 14px;

        .cell-top-right {
            float: right;
            color: #26a2ff;

            span {
                width: 200px;
            }

            span+span {
                margin-left: 30px;
            }
        }
    }

    .offen {
        margin-bottom: 20px;
        background-color: #e2dddd;


        .cell-list {
            position: relative;

            .swiper-btn {
                position: absolute;
                right: 0;
                top: 40%;
                margin-top: 5px;
                padding: 30px 20px;
                color: #26a2ff;
                font-size: 14px;
                background-color: #fff;
                z-index: 3;
                height: 80%;
                border: none;
                outline: none;
                transform: translateY(-50%);

                span {
                    top: 50%;
                    transform: translateY(50%);
                    border-radius: 3px;
                }

                span+span::before {
                    content: "| ";
                    margin: 0 24px;
                }
            }
        }
    }
</style>


