<template>
    <div class="life-more">
        <!-- 功能列表 -->
        <template v-for="item in cellList">
            <mt-cell key="item.id" :title="item.title" :to="item.href" is-link>
                <img src="../../assets/img/life/power.png" slot="icon" width="24" height="24">
            </mt-cell>
        </template>

        <div class="field-action">
            <mt-button type="default" size="large" @click="back">返回</mt-button>
        </div>

    </div>
</template>

<script>
    import * as servers from './../../services/life-cell';
    export default {
        name: 'life-more',
        data() {
            return {
                cellList: null,
                href: '#/life/power'
            };
        },
        created() {
            /** 获取cell数据 **/
            servers.getLifeCell()
                .then(response => {
                    this.cellList = response.data;
                });
        },
        methods: {
            back() {
                this.$router.back();
            }

        }
    };

</script>

<style scoped>

</style>


